.msg {
  display: flex;
  justify-content: center;
  min-height: 1em;
}

.msgbad {
  color: red;
}
.msggood {
  color: green;
}

.tabweights {
  margin-top: 10px;
  border-top: 1px solid #CCCCCC;
  overflow-y: scroll;
}

.tabweightstable {
  width: 100%;
}

.tabweightstablerow {
  padding: 3px 3px 3px 3px;
  border-width: 1px;
  border-color: #CCCCCC;
  border-style: solid;
}
.tabweightstablecol {
  padding: 3px 3px 3px 3px;
  border-width: 1px;
  border-color: #CCCCCC;
  border-style: solid;
}


.tagactive {
  background-color: #CCCCFF;
}
.weightactive {
  background-color: #CCFFCC;
}

.tabweightstagtext {
  text-align: center;
  border-radius: 3px;
  /*padding: 3px 3px 3px 3px */;
  margin-left: 3px;
  margin-right: 3px;
  border-width: 2px;
  border-style: solid;
  font-weight: bold;
  text-shadow: 1px 1px rgba(0,0,0,.3);
  font-size: 1.5em;
}

.tabgroup {
  overflow-y: scroll;
}

.tabgroupgood {
  color: green;
  font-weight: bold;
}
.tabgroupmeh {
  color: black;
}
.tabgroupbad {
  color: red;
}

.tabgroupname {
  text-align: right;
  padding-right: 10px;
}
.tabgroupnamepill {
  border: 1px solid darkgray;
  border-radius: 2px;
}

.tabgroupdate {
  text-align: right;
}

.tabgroupdead {
  text-align: right;
}

.tabgroupperc {
  text-align: right;
  padding-right: 2px;
}

.prefs {
  display: flex;
  flex-direction: row;
}

.prefslink {
  flex-grow: 1;
  color: #0000FF;
  text-decoration: underline;
  padding-left: 10px;
  padding-top: 10px;
}

.tabscontainer {
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.tabscontainertreatment {
  border: 1px solid #CCCCCC;
  border-radius: 3px;
  font-weight: bold;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: 5px;
  margin-right: 5px;
}

.tabscontainertag {
  display: flex;
  flex-direction: column;
}

.tabscontainertagentry {
  display: flex;
  flex-direction: row;
}

.tabscontainertitle {
  flex-shrink: 0;
  font-weight: bold;
}

.tabselector {
  display: flex;
  flex-shrink: 0;
  flex-direction: row;
}

.tabselectorbutton {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  min-height: 1.5em;
  border: 1px solid #CCCCCC;
  font-size: 1em;
  font-weight: bold;
}

.tabselectorbuttonactive {
  color: white;
  background: linear-gradient(to bottom, #7abcff 0%,#60abf8 44%,#4096ee 100%);
}

.weightdatebar{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 200px; /* not sure why this has to be here */
}

.weightdatebarcardtitle {
  font-weight: bold;
  font-size: 1em;
}

.weightdatebarcardouter {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 3px;
  margin-right: 3px;
  margin-top: 3px;
  padding-bottom: 3px;
}

.weightdatebarcardinner {
  display: flex;
  flex-direction: row;
}


.weightdatebarinput {
  text-align: center;
  border: 1px solid #CCCCCC;
  border-radius: 3px;
  margin-left: 5px;
}

.weightdatebarfilter {
  display: flex;
  align-items: center;
  text-align: center;
  font-weight: bold;
  border: 1px solid #CCCCCC;
  border-radius: 3px;
  margin-left: 5px;
  margin-right: 5px;
  min-height: 2.2em;
  padding-left: 5px;
  padding-right: 5px;
}

.weightdatebarinput{
  text-align: center;
  font-weight: bold;
  border: 1px solid #CCCCCC;
  border-radius: 3px;
}

.weightspane {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.keypad {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
}

.keypadrow {
  display: flex;
  flex-grow: 1;
  flex-shrink: 0;
}

.keypadbutton:hover {
  cursor: pointer;
}
.keypadbutton:active {
  background-color: #DDDDDD;
  background: linear-gradient(#EEEEEE,#FFFFFF);
}
.keypadbutton {
  flex-grow: 1;
  flex-shrink: 0;
  min-width: 33%;
  min-height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #CCCCCC;
  border-radius: 2px;
  font-family: sans-serif;
  font-size: 2em;
  color: #444444;
  background: linear-gradient(#FFFFFF,#EEEEEE);
}



.colorbar {
  display: flex;
  min-height: 2.5em;
}

.colorbutton {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1px;
  margin-left: 1px;
  border-radius: 5px;
  border: 1px solid #CCCCCC;
  box-shadow: 1px 1px grey;
  min-width: 25px;
}
.colorbutton:hover {
  cursor: pointer;
}


.tagbar {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  border-radius: 3px;
  border: 2px solid #DDDDDD;
  padding-left: 5px;
  padding-right: 5px;
  margin-top: 1px;
  margin-bottom: 1px;
}

.colortext {
  text-align: center;
  border-radius: 5px;
  border-width: 5px;
  border-style: solid;
  font-weight: bold;
  text-shadow: 1px 1px rgba(0,0,0,.3);
  font-size: 1.5em;
  width: 50%;
}

.numbertext {
  text-align: center;
  border: 0px;
  border-radius: 5px;
  border-width: 5px;
  border-style: solid;
  border-color: white;
  font-weight: bold;
  font-size: 1.5em;
  width: 50%;
}




.taginput {
  display: flex;
  flex-direction: column;
  /*flex-grow: 1;
  flex-shrink: 0; */
}

.taginputbuttonscontainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.taginputrowbutton {
  background: linear-gradient(to bottom, #7abcff 0%,#60abf8 44%,#4096ee 100%);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-grow: 0;
  margin-top: 5px;
  min-width: 30px;
  min-height: 1.5em;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
  font-size: 1.2em;
  font-weight: bold;
}

.taginputrowbuttonleft {
  margin-right: 10px;
}
.taginputrowbuttonright {
  margin-left: 10px;
}

.savebuttonenabled {
  color: white;
  background: linear-gradient(to bottom, #7abcff 0%,#60abf8 44%,#4096ee 100%);
}
.savebuttondisabled {
  color: #888888;
  background: #CCCCCC;
}

.savebutton {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  min-height: 1.5em;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
  font-size: 1.2em;
  font-weight: bold;
}

.weightinput {
  display: flex;
  flex-direction: column;
  /*flex-grow: 1;
  flex-shrink: 0; */
}

.weightinputdisplay {
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  border: 0px;
  font-weight: bold;
  font-size: 1.5em;
}
.weightinputdisplayinput {
  display: flex;
  flex-direction: row;
  text-align: right;
  border: 0px;
  font-weight: bold;
  font-size: 1.5em;
}


.weightinputbuttonscontainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.weightinputrowbutton {
  background: linear-gradient(to bottom, #cdeb8e 0%,#a5c956 100%);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-grow: 0;
  min-width: 30px;
  min-height: 1.5em;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
  font-size: 1.2em;
  font-weight: bold;
}

.weightinputrowbuttonleft {
  margin-right: 10px;
}
.weightinputrowbuttonright {
  margin-left: 10px;
}


.weightsavebuttonenabled {
  color: white;
  background: linear-gradient(to bottom, #cdeb8e 0%,#a5c956 100%);
}
.weightsavebuttondisabled {
  color: #888888;
  background: #CCCCCC;
}

.weightsavebutton {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  min-height: 1.5em;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
  font-size: 1.2em;
  font-weight: bold;
  padding-left: 5px;
  padding-right: 5px;
}

.App {
  height: 100vh;
  max-height: 100vh;
  width: 100vw;
  display: flex;
  align-items: stretch;
  align-content: stretch;
}

.inputs {
  display: flex;
}

.spacer {
  flex-grow: 1;
}

/* From https://purecss.io/grids/, allows padding and borders without breaking layout */
.pure-g > div {
  box-sizing: border-box;
}

.dbg {
  border: 1px solid red;
}

.App {
  height: 99%;
  width: 99%;
}

div {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


